import React, { useState } from 'react'
import { PageContent } from '../../components';
import { Dropdown } from 'primereact/dropdown';


const renderFlexInputs = (names, placeholders) => {
    return (
        <div className='flex items-center justify-between mb-6'>

            <div className='sm:w-[48%]'>

                <label htmlFor={names[0]} className='text-[18px] text-[#252525] font-medium'>{names[0]}</label>

                <input type='text' id={names[0]} className='p-3 w-full border-b border-b-[#b3b3b3] placeholder:text-[#b3b3b3]' placeholder={placeholders[0]} />

            </div>

            <div className='sm:w-[48%]'>

                <label htmlFor={names[1]} className='text-[18px] text-[#252525] font-medium'>{names[1]}</label>

                <input type='text' id={names[1]} className='p-3 w-full border-b border-b-[#b3b3b3] placeholder:text-[#b3b3b3]' placeholder={placeholders[1]} />

            </div>

        </div>
    )
}

export default function AddUser() {
    const [selectedCountry, setSelectedCountry] = useState(null);

    const countries = [
        { name: 'New York', code: 'NY' },
        { name: 'Rome', code: 'RM' },
        { name: 'London', code: 'LDN' },
        { name: 'Istanbul', code: 'IST' },
        { name: 'Paris', code: 'PRS' }
    ];


    return (

        <PageContent
            title={'User Form'}
            showActions={false}
        >

            <form onSubmit={e => e.preventDefault()} className='px-10'>

                {renderFlexInputs(['Name', 'Email'], ['Enter Name', 'Enter Email'])}

                {renderFlexInputs(['Password', 'Password Confirmation'], ['Enter Password', 'Enter Password Confirmation'])}

                {renderFlexInputs(['Address', 'Phone'], ['Enter Address', 'Enter Phone Number'])}

                {renderFlexInputs(['Zip', 'Card ID'], ['Enter Zip', 'Enter Card ID'])}

                <div className='flex items-center justify-between mb-6'>

                    <div className='sm:w-[48%]'>

                        <h2 className='text-[18px] text-[#252525] font-medium'>Country</h2>

                        <Dropdown value={selectedCountry} onChange={(e) => setSelectedCountry(e.value)} options={countries} optionLabel="name"
                            placeholder="Select Country" className="w-full p-2  !shadow-none !rounded-none !border-t-transparent !border-l-transparent !border-r-transparent" />

                    </div>
                    <div className='sm:w-[48%]'>

                        <h2 className='text-[18px] text-[#252525] font-medium'>Province</h2>

                        <Dropdown value={selectedCountry} onChange={(e) => setSelectedCountry(e.value)} options={countries} optionLabel="name"
                            placeholder="Select Province" className="w-full p-2  !shadow-none !rounded-none !border-t-transparent !border-l-transparent !border-r-transparent" />

                    </div>

                </div>

                <div className='flex items-center justify-between mb-6'>

                    <div className='sm:w-[48%]'>

                        <h2 className='text-[18px] text-[#252525] font-medium'>City</h2>

                        <Dropdown value={selectedCountry} onChange={(e) => setSelectedCountry(e.value)} options={countries} optionLabel="name"
                            placeholder="Select City" className="w-full p-2  !shadow-none !rounded-none !border-t-transparent !border-l-transparent !border-r-transparent" />

                    </div>
                    <div className='sm:w-[48%]'>

                        <h2 className='text-[18px] text-[#252525] font-medium'>Permissions</h2>

                        <Dropdown value={selectedCountry} onChange={(e) => setSelectedCountry(e.value)} options={countries} optionLabel="name"
                            placeholder="Select Permissions" className="w-full p-2  !shadow-none !rounded-none !border-t-transparent !border-l-transparent !border-r-transparent" />

                    </div>

                </div>


                <div className='flex items-center justify-end mt-10'>

                    <button className='bg-[#45B8EA] text-white py-[16px] px-32 rounded-full'>Submit</button>

                </div>

            </form>

        </PageContent>
    )
}
